}

.game-status p {
  margin: 5px 0;
}

.current-rune-overlay {
  position: absolute;
  top: 10px;
  left: 50%;
  transform: translateX(-50%) translateZ(0);
  background-color: rgba(0, 0, 0, 0.5);
  padding: 10px 20px;
  border-radius: 5px;
  color: white;
  font-size: 24px;
  font-weight: bold;
}

.feedback-overlay {
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  background-color: rgba(0, 0, 0, 0.5);
  padding: 20px 20px;
  border-radius: 5px;
  color: white;
  font-size: 20px;
  font-weight: thin;
  text-align: center;
  max-width: 80%;
  transition: color 0.2s, background 0.2s, box-shadow 0.2s;
  z-index: 10000;
  font-family: 'CuteNCuddly', cursive;
}

.feedback-overlay.shake-wrong {
  animation: shake 0.6s cubic-bezier(.36,.07,.19,.97) both;
}

.feedback-overlay.wrong {
  color: #fe7272e4;
  background: rgba(60,0,0,0.7);
  box-shadow: 0 0 20px #ff4444;
}

@keyframes shake {
  10%, 90% { transform: translateX(-50%) translateX(-2px); }
  20%, 80% { transform: translateX(-50%) translateX(4px); }
  30%, 50%, 70% { transform: translateX(-50%) translateX(-8px); }
  40%, 60% { transform: translateX(-50%) translateX(8px); }
  100% { transform: translateX(-50%); }
}

