/* Awesome background animations for game start screen */
.game-start::before, .game-start::after {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background-color: rgba(241, 151, 91, 0.03);
  opacity: 0.6;
  z-index: -1;
  animation: mystical-runes 25s linear infinite;
  pointer-events: none;
  border-radius: 45%;
  background-image: radial-gradient(
    circle at center,
    rgba(241, 151, 91, 0.05) 0%,
    rgba(67, 4, 255, 0.02) 50%,
    rgba(18, 16, 59, 0) 70%
  );
}

.game-start::after {
  top: -60%;
  left: -30%;
  width: 180%;
  height: 180%;
  background-color: rgba(67, 4, 255, 0.02);
  animation: mystical-runes-reverse 30s linear infinite;
  border-radius: 40%;
  background-image: radial-gradient(
    circle at center,
    rgba(67, 4, 255, 0.03) 0%,
    rgba(241, 151, 91, 0.02) 50%,
    rgba(18, 16, 59, 0) 70%
  );
}

/* Floating magical particles */
.game-start .particle {
  position: absolute;
  width: 6px;
  height: 6px;
  background: rgba(241, 151, 91, 0.6);
  border-radius: 50%;
  pointer-events: none;
  opacity: 0;
  animation: float-particle 8s ease-in-out infinite;
}

.game-start .particle:nth-child(2n) {
  width: 8px;
  height: 8px;
  background: rgba(67, 4, 255, 0.4);
  animation-delay: 1s;
  animation-duration: 10s;
}

.game-start .particle:nth-child(3n) {
  width: 4px;
  height: 4px;
  background: rgba(255, 255, 255, 0.6);
  animation-delay: 2s;
  animation-duration: 6s;
}

@keyframes float-particle {
  0% {
    transform: translateY(100px) translateX(0) rotate(0deg);
    opacity: 0;
  }
  20% {
    opacity: 0.8;
  }
  80% {
    opacity: 0.6;
  }
  100% {
    transform: translateY(-100px) translateX(20px) rotate(360deg);
    opacity: 0;
  }
}

@keyframes mystical-runes {
  0% {
    transform: rotate(0deg) scale(1);
  }
  100% {
    transform: rotate(360deg) scale(1.05);
  }
}

@keyframes mystical-runes-reverse {
  0% {
    transform: rotate(0deg) scale(1.05);
  }
  100% {
    transform: rotate(-360deg) scale(1);
  }
}

.game-title {
  font-family: 'NightForest', 'Cinzel', serif;
  font-size: 5rem;
  color: #f1975b;
  margin-bottom: 2rem;
  position: relative;
  display: inline-block;
}

/* Fancy decorative underline effect for game title */
.game-title::after {
  content: '';
  position: absolute;
  width: 200%;
  height: 35px;
  bottom: -25px;
  left: -50%;
  background-color: transparent;
  background-image: var(--underline-svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  opacity: 1;
  filter: invert(75%) sepia(39%) saturate(3530%) hue-rotate(334deg) brightness(101%) contrast(93%);
}

.game-subtitle {
  color: #fff;
  margin-bottom: 30px;
}
