/* AboutPage.css */
@import url('./common.css');
.about-page {
  width: 100%;
  min-height: 100vh;
  padding-top: 60px; /* For navbar */
  font-family: 'Poppins', sans-serif;
  position: relative;
  overflow-x: hidden;
  background-color: #1a1433; /* Darker base background */
  perspective: 1000px;
}

.about-page-container {
  width: 100%;
  min-height: 100vh;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  overflow: hidden;
}

.about-page-bg-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url('./assets/abstract-bg-about.svg');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  opacity: 0.7;
  z-index: -2;
  filter: saturate(1.5) brightness(1.2);
  animation: subtle-pulse 15s infinite alternate;
}

@keyframes subtle-pulse {
  0% {
    filter: saturate(1.5) brightness(1.2) hue-rotate(0deg);
  }
  100% {
    filter: saturate(1.8) brightness(1.3) hue-rotate(10deg);
  }
}

/* Floating mist animation moved to common.css */

/* Magic particles */
.magic-particle {
  position: absolute;
  border-radius: 50%;
  opacity: 0.6;
  pointer-events: none;
  z-index: 1;
  box-shadow: 0 0 10px 2px currentColor;
  filter: blur(0.5px);
}

/* Default flow pattern - circular motion */
.magic-particle-type-0 {
  animation: flow-particle-circular 8s infinite linear;
}

/* Wavy horizontal flow */
.magic-particle-type-1 {
  animation: flow-particle-wavy 10s infinite ease-in-out;
}

/* Spiral flow */
.magic-particle-type-2 {
  animation: flow-particle-spiral 12s infinite ease-in-out;
}

/* Circular flow animation */
@keyframes flow-particle-circular {
  0% {
    transform: translateY(0) translateX(0) scale(1) rotate(0deg);
    opacity: 0.4;
    filter: blur(1px);
  }
  25% {
    transform: translateY(-20px) translateX(20px) scale(1.1) rotate(90deg);
    opacity: 0.7;
    filter: blur(0.5px);
  }
  50% {
    transform: translateY(0) translateX(40px) scale(1.2) rotate(180deg);
    opacity: 0.8;
    filter: blur(0.3px);
  }
  75% {
    transform: translateY(20px) translateX(20px) scale(1.1) rotate(270deg);
    opacity: 0.7;
    filter: blur(0.5px);
  }
  100% {
    transform: translateY(0) translateX(0) scale(1) rotate(360deg);
    opacity: 0.4;
    filter: blur(1px);
  }
}

/* Wavy horizontal flow animation */
@keyframes flow-particle-wavy {
  0% {
    transform: translateY(0) translateX(-20px) scale(1);
    opacity: 0.4;
    filter: blur(1px);
  }
  25% {
    transform: translateY(15px) translateX(0) scale(1.1);
    opacity: 0.7;
    filter: blur(0.5px);
  }
  50% {
    transform: translateY(0) translateX(20px) scale(1.2);
    opacity: 0.8;
    filter: blur(0.3px);
  }
  75% {
    transform: translateY(-15px) translateX(0) scale(1.1);
    opacity: 0.7;
    filter: blur(0.5px);
  }
  100% {
    transform: translateY(0) translateX(-20px) scale(1);
    opacity: 0.4;
    filter: blur(1px);
  }
}

/* Spiral flow animation */
@keyframes flow-particle-spiral {
  0% {
    transform: translateY(0) translateX(0) scale(1) rotate(0deg);
    opacity: 0.4;
    filter: blur(1px);
  }
  20% {
    transform: translateY(-10px) translateX(10px) scale(1.05) rotate(72deg);
    opacity: 0.5;
    filter: blur(0.8px);
  }
  40% {
    transform: translateY(-20px) translateX(0) scale(1.1) rotate(144deg);
    opacity: 0.6;
    filter: blur(0.6px);
  }
  60% {
    transform: translateY(-10px) translateX(-10px) scale(1.15) rotate(216deg);
    opacity: 0.7;
    filter: blur(0.4px);
  }
  80% {
    transform: translateY(0) translateX(-20px) scale(1.1) rotate(288deg);
    opacity: 0.6;
    filter: blur(0.6px);
  }
  100% {
    transform: translateY(0) translateX(0) scale(1) rotate(360deg);
    opacity: 0.4;
    filter: blur(1px);
  }
}

/* Reveal animation for elements */
.reveal-element {
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 0.8s ease, transform 0.8s ease;
}

.revealed {
  opacity: 1;
  transform: translateY(0);
}

/* About Fablesmiths Section */
.about-fablesmiths-section {
  width: 100vw;
  margin-left: calc(-50vw + 50%);
  padding: 6rem 0 5rem;
  background-color: rgba(46, 38, 154, 0.85);
  color: white;
  display: flex;
  justify-content: center;
  box-sizing: border-box;
  position: relative;
  backdrop-filter: blur(8px);
  position: relative;
  overflow: hidden;
  border-bottom: none;
}

.about-fablesmiths-section::before {
  content: '';
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 100%;
  height: 3px;
  background: linear-gradient(90deg, transparent, rgba(175, 118, 255, 0.8), rgba(255, 130, 255, 0.8), rgba(175, 118, 255, 0.8), transparent);
  z-index: 1;
  animation: shimmer 3s infinite linear;
}

@keyframes shimmer {
  0% {
    background-position: -200% 0;
  }
  100% {
    background-position: 200% 0;
  }
}

.about-content {
  width: 100%;
  max-width: 1000px;
  padding: 0 2rem;
  text-align: center;
}

.about-heading {
  font-size: 3.8rem;
  margin: 30px 0;
  margin-bottom: 2rem;
  font-family: 'NightForest', 'ChalkaholicDemo', 'SteakHouse', 'BrightGesture', 'Cinzel', serif;
  font-weight: normal;
  letter-spacing: 0.03em;
  line-height: 1.3;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.4);
  color: #e3abff; /* Updated color */
  position: relative;
  display: inline-block;
}

/* Fancy decorative underline effect */
.about-heading::after {
  content: '';
  position: absolute;
  width: 200%;
  height: 35px;
  bottom: -45px; /* Further increased gap between header and underline */
  left: -50%;
  background-color: transparent;
  background-image: url('./assets/underline0.svg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  opacity: 0.9; /* More saturated */
  filter: invert(75%) sepia(60%) saturate(2000%) hue-rotate(270deg) brightness(100%) contrast(110%);
}

.about-text {
  font-size: 1.4rem;
  line-height: 1.6;
  margin-bottom: 2rem;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  font-family: 'Lato', sans-serif;
  color: #f0f0f0; /* Brighter color */
}

/* Team Section */
.team-section {
  width: 100vw;
  margin-left: calc(-50vw + 50%);
  padding: 1rem 0 4rem; /* Add bottom padding to prevent footer overlap */
  margin-top: -2px; /* Remove gap between sections */
  margin-bottom: 0;
  background-color: rgba(42, 42, 122, 0.9);
  color: white;
  display: flex;
  justify-content: center;
  box-sizing: border-box;
  position: relative;
  backdrop-filter: blur(8px);
  position: relative;
  overflow: hidden;
  border-top: 1px solid rgba(138, 43, 226, 0.5);
}

/* Magical particles */
.team-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: radial-gradient(circle, rgba(255, 255, 255, 0.15) 1px, transparent 1px);
  background-size: 30px 30px;
  opacity: 0.5;
  z-index: 0;
  animation: float 60s infinite linear;
  pointer-events: none;
}

@keyframes float {
  0% {
    transform: translateY(0) rotate(0deg);
  }
  100% {
    transform: translateY(-100px) rotate(360deg);
  }
}

.team-content {
  width: 100%;
  max-width: 1200px;
  padding: 0 2rem;
  text-align: center;
}

.team-title {
  font-family: 'NightForest', 'ChalkaholicDemo', 'SteakHouse', 'BrightGesture', 'Cinzel', serif;
  font-weight: normal;
  letter-spacing: 0.03em;
  line-height: 1.3;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.4);
  font-size: 3.8rem;
  margin: 30px 0;
  margin-bottom: 2rem;
  color: #e3abff; /* Updated color */
  position: relative;
  display: inline-block;
}

/* Fancy decorative underline effect */
.team-title::after {
  content: '';
  position: absolute;
  width: 200%;
  height: 35px;
  bottom: -45px; /* Further increased gap between header and underline */
  left: -50%;
  background-color: transparent;
  background-image: url('./assets/underline0.svg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  opacity: 0.9; /* More saturated */
  filter: invert(75%) sepia(60%) saturate(2000%) hue-rotate(270deg) brightness(100%) contrast(110%);
}

.team-subtitle {
  font-size: 1.4rem;
  margin-bottom: 4rem;
  color: #e0e0ff; /* Brighter color */
  font-family: 'Lato', sans-serif;
  font-weight: bold;
}

.team-members {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  margin-top: 3rem;
  max-width: 1000px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 2rem; /* Add bottom margin */
  padding-bottom: 1rem; /* Add bottom padding */
}

.team-member {
  flex: 0 0 calc(33.333% - 2rem);
  min-width: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 2rem; /* Add bottom margin to each team member */
  transition: transform 0.3s ease;
}

.team-member:hover {
  transform: translateY(-5px);
}

.member-avatar {
  width: 150px;
  height: 150px;
  background-color: #e0e0e0;
  border-radius: 10px;
  margin-bottom: 1.5rem;
  transition: all 0.3s ease;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
  position: relative;
  overflow: hidden;
}

.member-avatar::after {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: 0.5s;
}

.team-member:hover .member-avatar::after {
  left: 100%;
}

.team-member:hover .member-avatar {
  transform: translateY(-8px);
  box-shadow: 0 15px 25px rgba(0, 0, 0, 0.4), 0 0 15px rgba(138, 43, 226, 0.4);
}

.member-name {
  font-family: 'OugkehRoundRegular', 'NightForest', 'Lato','Cinzel', serif;
  font-weight: thin;
  letter-spacing: 0.03em;
  line-height: 1.3;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.4);
  font-size: 1.8rem;
  margin: 0.5rem 0;
  color: #e3abff; /* Updated color */
}

.member-bio {
  font-size: 2rem;
  color: #f0f0f0; /* Brighter color */
  line-height: 1.5;
  font-family: 'Lato', sans-serif;
}

.member-role {
  font-family: 'Lato','Cinzel', serif;
  
  font-size: 1.3rem;
  color: #c8c8ff;
  margin-bottom: 1rem;
}

.member-links {
  display: flex;
  gap: 1rem;
  margin-top: 0.5rem;
}

.member-link {
  margin: 0;
  padding: 0.5rem 1rem;
  background-color: rgba(255, 255, 255, 0.1);
  color: white;
  text-decoration: none;
  border-radius: 4px;
  transition: all 0.3s ease;
  font-family: 'OugkehRoundRegular', cursive;
  font-weight: bold;
  font-size: 1.2rem;
  letter-spacing: 2px;
  border: 1px solid rgba(138, 43, 226, 0.3);
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.member-link::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(138, 43, 226, 0.2), transparent);
  transition: 0.5s;
  z-index: -1;
}

.member-link:hover::before {
  left: 100%;
}

.member-link:hover {
  background-color: rgba(138, 43, 226, 0.3);
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2), 0 0 5px rgba(138, 43, 226, 0.5);
  border-color: rgba(138, 43, 226, 0.6);
}

.learn-more-btn {
  display: inline-block;
  padding: 12px 25px;
  margin-top: 20px;
  font-size: 1.1rem;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 1px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s ease;
  background-color: #6c5ffd;
  color: white;
  box-shadow: 0 4px 15px rgba(108, 95, 253, 0.4);
  font-family: 'CuteNCuddly', cursive;
}

/* Responsive adjustments */
@media (max-width: 1200px) {
  .team-member {
    flex: 0 0 calc(33.333% - 2rem);
  }
}

@media (max-width: 768px) {
  .about-heading {
    font-size: 3rem;
  }
  
  .team-title {
    font-size: 2.5rem;
  }
  
  .team-member {
    flex: 0 0 calc(50% - 2rem);
  }
}

@media (max-width: 480px) {
  .about-heading {
    font-size: 2.5rem;
  }
  
  .team-title {
    font-size: 2rem;
  }
  
  .team-member {
    flex: 0 0 100%;
  }
  
  .member-avatar {
    width: 120px;
    height: 120px;
  }
}

/* Footer styles */
.site-footer {
  width: 100vw;
  margin-left: calc(-50vw + 50%);
  padding: 2rem 0;
  background-color: rgba(20, 20, 50, 0.95);
  color: white;
  display: flex;
  justify-content: center;
  box-sizing: border-box;
  position: relative;
  backdrop-filter: blur(8px);
  margin-top: -2px; /* Remove gap between sections */
  border-top: 1px solid rgba(138, 43, 226, 0.3);
  position: relative;
  overflow: hidden;
}

.site-footer::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.5), transparent);
  animation: shimmer 3s infinite linear;
}

.footer-content {
  width: 100%;
  max-width: 1200px;
  padding: 0 2rem;
  text-align: center;
}

.copyright {
  font-size: 1rem;
  color: rgba(255, 255, 255, 0.7);
  font-family: 'Lato', sans-serif;
  position: relative;
  display: inline-block;
}

.copyright::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 25%;
  width: 50%;
  height: 1px;
  background: linear-gradient(90deg, transparent, rgba(138, 43, 226, 0.5), transparent);
}
