.slideshow-container {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  border-radius: 8px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.5);
  border: 2px solid rgba(138, 43, 226, 0.6);
  background-color: rgba(30, 25, 50, 0.85);
  transition: all 0.3s ease;
  cursor: pointer;
}

/* Elegant corner accents */
.slideshow-container::before,
.slideshow-container::after {
  content: '';
  position: absolute;
  width: 20px;
  height: 20px;
  z-index: 10;
  pointer-events: none;
}

.slideshow-container::before {
  top: 10px;
  left: 10px;
  border-top: 1px solid rgba(255, 130, 255, 0.8);
  border-left: 1px solid rgba(255, 130, 255, 0.8);
}

.slideshow-container::after {
  bottom: 10px;
  right: 10px;
  border-bottom: 1px solid rgba(138, 43, 226, 0.8);
  border-right: 1px solid rgba(138, 43, 226, 0.8);
}

/* Add similar corner accents to the other corners */
.slideshow-container .corner-top-right {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 20px;
  height: 20px;
  border-top: 1px solid rgba(255, 130, 255, 0.8);
  border-right: 1px solid rgba(255, 130, 255, 0.8);
  z-index: 10;
  pointer-events: none;
}

.slideshow-container .corner-bottom-left {
  position: absolute;
  bottom: 10px;
  left: 10px;
  width: 20px;
  height: 20px;
  border-bottom: 1px solid rgba(138, 43, 226, 0.8);
  border-left: 1px solid rgba(138, 43, 226, 0.8);
  z-index: 10;
  pointer-events: none;
}

.slideshow-container:hover {
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.6), 0 0 15px rgba(138, 43, 226, 0.3);
  border-color: rgba(255, 130, 255, 0.8);
}

.slideshow-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease-in-out, opacity 0.5s ease-in-out;
}

.slideshow-container:hover .slideshow-image {
  transform: scale(1.03);
}

/* Zoom functionality */
.zoom-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.9);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  cursor: zoom-out;
}

.zoom-container {
  position: relative;
  max-width: 90%;
  max-height: 90%;
  overflow: hidden;
  box-shadow: 0 0 30px rgba(138, 43, 226, 0.5);
  border-radius: 4px;
}

.zoom-image {
  max-width: 100%;
  max-height: 90vh;
  object-fit: contain;
  cursor: default;
}

.zoom-close-button {
  position: absolute;
  top: 15px;
  right: 15px;
  width: 40px;
  height: 40px;
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.zoom-close-button:hover {
  background-color: rgba(138, 43, 226, 0.7);
  transform: scale(1.1);
}

/* Zoom hint */
.zoom-hint {
  position: absolute;
  bottom: 50px;
  left: 50%;
  transform: translateX(-50%);
  background-color: rgba(0, 0, 0, 0.6);
  color: white;
  padding: 5px 12px;
  border-radius: 20px;
  font-size: 12px;
  opacity: 0;
  transition: opacity 0.3s ease;
  pointer-events: none;
}

.slideshow-container:hover .zoom-hint {
  opacity: 0.8;
}

.slideshow-controls {
  position: absolute;
  bottom: 15px;
  left: 0;
  right: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 15px;
  z-index: 20;
}

.slideshow-button {
  background-color: rgba(30, 25, 50, 0.7);
  color: #e3abff;
  border: none;
  border-radius: 50%;
  width: 35px;
  height: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  font-size: 18px;
  transition: all 0.3s ease;
  box-shadow: 0 0 10px rgba(138, 43, 226, 0.3);
  position: relative;
  overflow: hidden;
}

.slideshow-button::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, transparent 45%, rgba(255, 255, 255, 0.2) 50%, transparent 55%);
  background-size: 200% 200%;
  animation: shimmer 3s infinite;
  pointer-events: none;
}

.slideshow-button:hover {
  background-color: rgba(138, 43, 226, 0.5);
  transform: scale(1.1);
  box-shadow: 0 0 15px rgba(255, 130, 255, 0.5);
}

@keyframes shimmer {
  0% { background-position: 200% 0; }
  100% { background-position: -200% 0; }
}

.slideshow-indicators {
  display: flex;
  justify-content: center;
  gap: 8px;
}

.slideshow-dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.3);
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  border: 1px solid rgba(255, 130, 255, 0.3);
}

.slideshow-dot::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 4px;
  height: 4px;
  background-color: rgba(255, 255, 255, 0.7);
  border-radius: 50%;
  transform: translate(-50%, -50%);
  transition: all 0.3s ease;
}

.slideshow-dot:hover {
  background-color: rgba(138, 43, 226, 0.4);
  transform: scale(1.2);
}

.slideshow-dot.active {
  background-color: rgba(255, 130, 255, 0.7);
  box-shadow: 0 0 8px rgba(255, 130, 255, 0.9);
  transform: scale(1.2);
}

.slideshow-dot.active::after {
  background-color: white;
  width: 6px;
  height: 6px;
  box-shadow: 0 0 4px white;
}

@media (max-width: 768px) {
  .slideshow-button {
    width: 25px;
    height: 25px;
    font-size: 14px;
  }
  
  .slideshow-dot {
    width: 8px;
    height: 8px;
  }
}
