/* ContactPage.css */
@import url('./common.css');

/* Magic particles animations */
@keyframes cp-flow-particle-circular {
  0% {
    transform: translateY(0) translateX(0) scale(1) rotate(0deg);
    opacity: 0.4;
    filter: blur(1px);
  }
  25% {
    transform: translateY(-20px) translateX(20px) scale(1.1) rotate(90deg);
    opacity: 0.7;
    filter: blur(0.5px);
  }
  50% {
    transform: translateY(0) translateX(40px) scale(1.2) rotate(180deg);
    opacity: 0.8;
    filter: blur(0.3px);
  }
  75% {
    transform: translateY(20px) translateX(20px) scale(1.1) rotate(270deg);
    opacity: 0.7;
    filter: blur(0.5px);
  }
  100% {
    transform: translateY(0) translateX(0) scale(1) rotate(360deg);
    opacity: 0.4;
    filter: blur(1px);
  }
}

@keyframes cp-flow-particle-wavy {
  0% {
    transform: translateY(0) translateX(-20px) scale(1);
    opacity: 0.4;
    filter: blur(1px);
  }
  25% {
    transform: translateY(15px) translateX(0) scale(1.1);
    opacity: 0.7;
    filter: blur(0.5px);
  }
  50% {
    transform: translateY(0) translateX(20px) scale(1.2);
    opacity: 0.8;
    filter: blur(0.3px);
  }
  75% {
    transform: translateY(-15px) translateX(0) scale(1.1);
    opacity: 0.7;
    filter: blur(0.5px);
  }
  100% {
    transform: translateY(0) translateX(-20px) scale(1);
    opacity: 0.4;
    filter: blur(1px);
  }
}

@keyframes cp-flow-particle-spiral {
  0% {
    transform: translateY(0) translateX(0) scale(1) rotate(0deg);
    opacity: 0.4;
    filter: blur(1px);
  }
  20% {
    transform: translateY(-10px) translateX(10px) scale(1.05) rotate(72deg);
    opacity: 0.5;
    filter: blur(0.8px);
  }
  40% {
    transform: translateY(-20px) translateX(0) scale(1.1) rotate(144deg);
    opacity: 0.6;
    filter: blur(0.6px);
  }
  60% {
    transform: translateY(-10px) translateX(-10px) scale(1.15) rotate(216deg);
    opacity: 0.7;
    filter: blur(0.4px);
  }
  80% {
    transform: translateY(0) translateX(-20px) scale(1.1) rotate(288deg);
    opacity: 0.6;
    filter: blur(0.6px);
  }
  100% {
    transform: translateY(0) translateX(0) scale(1) rotate(360deg);
    opacity: 0.4;
    filter: blur(1px);
  }
}

.cp-contact-page {
  width: 100%;
  min-height: 100vh;
  padding-top: 0; /* Removed padding-top */
  font-family: 'Poppins', sans-serif;
  position: relative;
  background-color: #1a1433; /* Dark fantasy background */
}

.cp-contact-page-container {
  width: 100%;
  min-height: 100vh;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  overflow: hidden;
}

.cp-contact-page-bg-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  opacity: 0.4;
  z-index: -2;
  filter: saturate(1.2) brightness(0.8);
}

/* Magic particles */
.cp-magic-particle {
  position: absolute;
  border-radius: 50%;
  opacity: 0.6;
  pointer-events: none;
  z-index: 1;
  box-shadow: 0 0 10px 2px currentColor;
  filter: blur(0.5px);
}

.cp-magic-particle-type-0 {
  animation: cp-flow-particle-circular 8s infinite linear;
}

.cp-magic-particle-type-1 {
  animation: cp-flow-particle-wavy 10s infinite ease-in-out;
}

.cp-magic-particle-type-2 {
  animation: cp-flow-particle-spiral 12s infinite ease-in-out;
}

/* Floating mist animation moved to common.css */

/* Root variables moved to common.css */

.cp-contact-page-section {
  width: 100%;
  padding: 6rem 0 4rem 0; /* Reduced top padding from 12rem to 6rem */
  color: #3a2a1d;
  display: flex;
  justify-content: center;
  box-sizing: border-box;
  position: relative;
  z-index: 1;
}

.cp-contact-content {
  width: 100%;
  max-width: 1400px; /* Further increased to 1400px */
  margin: 0 auto;
  padding: 0; /* Removed padding completely */
  text-align: center;
  position: relative;
  z-index: 1;
}

.cp-contact-title {
  font-family: 'NightForest', 'Cinzel', serif;
  font-size: 3.5rem;
  margin-bottom: 2.5rem; /* Increased margin-bottom to make room for underline */
  color: #e3abff;
  font-weight: normal;
  letter-spacing: 0.03em;
  line-height: 1.3;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.4);
  position: relative; /* Added position relative for the underline */
  display: inline-block; /* Added inline-block to properly contain the underline */
}

.cp-contact-subtitle {
  font-size: 1.2rem;
  margin-bottom: 2rem;
  color: #e0e0ff;
  font-family: 'Lato', sans-serif;
}

.cp-contact-title::after {
  content: '';
  position: absolute;
  width: 200%;
  height: 35px;
  bottom: -25px; /* Adjusted from -45px to -25px to position closer to text */
  left: -50%;
  background-color: transparent;
  background-image: var(--underline-svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  opacity: 0.9; /* More saturated */
  filter: invert(75%) sepia(60%) saturate(2000%) hue-rotate(270deg) brightness(100%) contrast(110%);
}

.cp-contact-subtitle {
  font-size: 1.2rem;
  margin-bottom: 2rem; /* Reduced from 4rem to 2rem for better spacing */
  color: #e0e0ff; /* Brighter color */
  font-family: 'Lato', sans-serif;
  font-weight: normal;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.cp-contact-form-container {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.cp-contact-form-wrapper {
  width: 65%; /* Further increased to 65% */
  padding-right: 0; /* Removed right padding */
}

.cp-contact-image-container {
  width: 30%; /* Adjusted to complement the 65% form width */
  display: flex;
  justify-content: center;
  align-items: center;
}

.cp-contact-image {
  max-width: 140%; /* Further increased to make image larger */
  height: auto;
  border-radius: 0;
  box-shadow: none;
  transform: none;
  transition: transform 0.3s ease;
}

.cp-contact-image:hover {
  transform: scale(1.02); /* Subtle scale effect only */
}

.cp-contact-form {
  max-width: 100%;
  margin: 0 auto;
  text-align: left;
  background-color: rgba(255, 255, 255, 0.1);
  padding: 2rem;
  border-radius: 12px; /* Increased border radius */
  border: 1px solid rgba(138, 43, 226, 0.3);
  position: relative;
}

/* Add clearfix to handle the floating button */
.cp-contact-form::after {
  content: "";
  display: table;
  clear: both;
}

.cp-form-group {
  margin-bottom: 1.5rem;
  position: relative;
}

/* Form success/error messages */
.cp-form-message {
  margin-top: 1.5rem;
  padding: 1rem;
  border-radius: 8px;
  font-weight: 500;
  text-align: center;
  animation: fadeIn 0.5s ease-in-out;
  clear: both;
  width: 80%;
  max-width: 500px;
  margin-left: auto;
  margin-right: auto;
}

.cp-success {
  background-color: rgba(76, 175, 80, 0.3);
  color: #1b9e25;
  border: 1px solid rgba(76, 175, 80, 0.5);
  box-shadow: 0 0 15px rgba(76, 175, 80, 0.3);
  font-weight: 600;
}

.cp-error {
  background-color: rgba(244, 67, 54, 0.2);
  color: #d32f2f;
  border: 1px solid rgba(244, 67, 54, 0.4);
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(-10px); }
  to { opacity: 1; transform: translateY(0); }
}

.cp-form-label {
  display: block;
  margin-bottom: 0.5rem;
  font-size: 1.1rem;
  color: #e0e0ff;
  font-weight: 500;
  font-family: 'Cinzel', serif;
}

.cp-form-input,
.cp-form-textarea {
  width: 95%;
  padding: 0.8rem;
  font-size: 1rem;
  background-color: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(138, 43, 226, 0.4);
  border-radius: 5px;
  color: #e0e0ff;
  transition: all 0.3s ease;
  font-family: 'Lato', sans-serif;
}

.cp-form-input:focus,
.cp-form-textarea:focus {
  outline: none;
  border-color: rgba(138, 43, 226, 0.8);
  box-shadow: 0 0 0 2px rgba(138, 43, 226, 0.2);
  background-color: rgba(255, 255, 255, 0.2);
}

.cp-form-textarea {
  min-height: 150px;
  resize: vertical;
}

/* Button container for TO label and Send Message button */
.cp-button-container {
  display: flex;
  flex-direction: row; /* Changed to row to place elements on same line */
  justify-content: space-between; /* Space between TO label and button */
  align-items: center; /* Center align vertically */
  margin-top: 1.5rem;
  width: 100%;
  clear: both;
}

/* TO label styling */
.cp-to-label {
  display: flex;
  align-items: center;
  justify-content: flex-start; /* Left align content */
  color: white;
  font-size: 1.1rem;
  font-weight: 600;
  letter-spacing: 0.5px;
  font-family: 'OugkehRoundRegular', 'Cinzel', serif;
  transition: all 0.3s ease;
  opacity: 0.9;
  margin-left: 1rem; /* Add some left margin */
}

/* Add subtle glow effect when hovering over the Send Message button */
.cp-button-container:hover .cp-to-label {
  opacity: 1;
  text-shadow: 0 0 8px rgba(255, 255, 255, 0.6);
}

/* Logo in the button container */
.cp-button-logo {
  width: 40px; /* Increased size */
  height: 40px; /* Increased size */
  filter: brightness(1) saturate(0) invert(1); /* Make logo white */
  transition: all 0.3s ease;
}

/* Add subtle rotation and glow to logo on hover */
.cp-button-container:hover .cp-button-logo {
  transform: rotate(5deg) scale(1.15);
  filter: brightness(1) saturate(0) invert(1) drop-shadow(0 0 5px rgba(255, 255, 255, 0.9));
}

.cp-send-message-btn {
  background: linear-gradient(135deg, rgba(138, 43, 226, 0.9), rgba(108, 95, 253, 0.8));
  color: white;
  border: none;
  padding: 1.2rem 2.5rem; /* Increased padding for bigger button */
  font-size: 1.3rem; /* Larger font */
  border-radius: 30px; /* Pill shape */
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: 600;
  font-family: 'OugkehRoundRegular', 'Cinzel', serif;
  position: relative;
  overflow: hidden;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
  box-shadow: 0 4px 12px rgba(108, 95, 253, 0.4), 0 0 15px rgba(138, 43, 226, 0.3);
  letter-spacing: 0.8px;
  min-width: 220px; /* Minimum width */
  border: 1px solid rgba(255, 255, 255, 0.1);
  margin-right: 1rem; /* Add some right margin */
}

.cp-send-message-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  transition: 0.5s;
  z-index: 1;
}

.cp-send-message-btn::after {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(circle, rgba(255, 255, 255, 0.1) 0%, transparent 70%);
  transform: scale(0);
  opacity: 0;
  transition: transform 0.5s ease, opacity 0.5s ease;
  z-index: 0;
}

.cp-send-message-btn:hover {
  background: linear-gradient(135deg, rgba(251, 243, 131, 0.9), rgba(249, 233, 7, 0.8));
  color: #3232ff;
  transform: translateY(-3px);
  box-shadow: 0 8px 20px rgba(230, 220, 75, 0.5), 0 0 25px rgba(230, 220, 75, 0.4);
  border-color: rgba(230, 220, 75, 0.3);
}

.cp-send-message-btn:active {
  transform: translateY(-1px);
  box-shadow: 0 4px 10px rgba(230, 220, 75, 0.4);
  transition: all 0.1s ease;
}

.cp-send-message-btn:hover::before {
  left: 100%;
}

.cp-send-message-btn:hover::after {
  transform: scale(1);
  opacity: 1;
}

.cp-send-message-btn span {
  position: relative;
  z-index: 2;
}

.cp-contact-info {
  display: flex !important;
  justify-content: space-around;
  flex-wrap: wrap;
  margin-top: 4rem;
  padding: 2rem;
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  border: 1px solid rgba(138, 43, 226, 0.3);
  position: relative;
  z-index: 10;
  width: 100%;
  clear: both;
  opacity: 1 !important;
  visibility: visible !important;
  margin-bottom: 2rem;
}

/* Reveal animation for elements */
.cp-reveal-element {
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 0.8s ease, transform 0.8s ease;
}

.revealed {
  opacity: 1;
  transform: translateY(0);
}

.cp-contact-info-item {
  margin: 1rem;
  text-align: center;
  flex: 1;
  min-width: 200px;
  position: relative;
  padding: 2rem 1.5rem; /* Increased top/bottom padding */
  border-radius: 12px; /* Increased border radius */
  transition: all 0.3s ease;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.07), rgba(138, 43, 226, 0.1)); /* Gradient background */
  position: relative;
  overflow: hidden;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2), 0 0 20px rgba(138, 43, 226, 0.3); /* Enhanced shadow for visibility */
  border: 1px solid rgba(138, 43, 226, 0.3); /* More visible border */
  z-index: 10;
  display: block !important;
  visibility: visible !important;
  opacity: 1 !important;
}

.cp-contact-info-item:hover {
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.1), rgba(138, 43, 226, 0.15)); /* Enhanced gradient on hover */
  transform: translateY(-5px);
  box-shadow: 0 8px 20px rgba(138, 43, 226, 0.3), 0 0 15px rgba(138, 43, 226, 0.2); /* Enhanced glow effect */
  border-color: rgba(138, 43, 226, 0.4); /* Brighter border on hover */
}

/* Removed the logo watermark */

.cp-contact-info-item h3 {
  font-size: 1.8rem; /* Increased from 1.5rem to 1.8rem */
  margin-bottom: 1rem;
  color: #e3abff;
  font-family: 'NightForest', 'Cinzel', serif;
  font-weight: normal;
  position: relative;
  display: inline-block;
  z-index: 1; /* Ensure heading appears above the watermark */
}

.cp-contact-info-item h3::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: -8px;
  left: 0;
  background: linear-gradient(to right, transparent, rgba(138, 43, 226, 0.7), transparent); /* Gradient underline */
  transform: scaleX(0.7);
  transform-origin: center;
  transition: transform 0.3s ease, background 0.3s ease;
  box-shadow: 0 0 8px rgba(138, 43, 226, 0.4); /* Subtle glow */
}

.cp-contact-info-item:hover h3::after {
  transform: scaleX(1);
}

.cp-contact-info-item p {
  font-size: 1.2rem; /* Increased from 1.1rem to 1.2rem */
  color: #e0e0ff;
  margin-bottom: 1rem;
  position: relative;
  z-index: 1; /* Ensure text appears above the watermark */
}

.cp-game-links {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 1rem;
}

.cp-game-link {
  color: #e0e0ff;
  text-decoration: none;
  font-size: 1.2rem; /* Increased font size */
  font-weight: 500; /* Medium weight */
  letter-spacing: 0.5px; /* Spacing for elegance */
  transition: all 0.3s ease;
  padding: 1.2rem 2rem; /* Increased padding for bigger button */
  background: linear-gradient(135deg, rgba(138, 43, 226, 0.2), rgba(138, 43, 226, 0.1)); /* Gradient background */
  border: 1px solid rgba(138, 43, 226, 0.4);
  border-radius: 30px; /* Pill shape */
  font-family: 'OugkehRoundRegular', 'Cinzel', serif;
  display: flex;
  align-items: center;
  gap: 1rem; /* Increased gap */
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2), 0 0 5px rgba(138, 43, 226, 0.2); /* Subtle shadow */
  position: relative;
  overflow: hidden;
  min-width: 220px; /* Minimum width for consistency */
  justify-content: center; /* Center content */
}

.cp-game-link:hover {
  color: white;
  background: linear-gradient(135deg, rgba(138, 43, 226, 0.9), rgba(138, 43, 226, 0.7)); /* Enhanced gradient on hover */
  transform: translateY(-3px);
  box-shadow: 0 6px 15px rgba(138, 43, 226, 0.5), 0 0 20px rgba(138, 43, 226, 0.3); /* Enhanced glow effect */
}

.cp-game-link:active {
  transform: translateY(-1px);
  box-shadow: 0 3px 10px rgba(138, 43, 226, 0.4);
  transition: all 0.1s ease;
}

.cp-game-link::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(circle, rgba(255, 255, 255, 0.1) 0%, transparent 70%);
  transform: scale(0);
  opacity: 0;
  transition: transform 0.5s ease, opacity 0.5s ease;
}

.cp-game-link:hover::before {
  transform: scale(1);
  opacity: 1;
}

/* Moved to be with the .cp-platform-icon selector */

.cp-platform-icon {
  width: 35px; /* Increased size */
  height: 35px; /* Increased size */
  object-fit: contain;
  filter: brightness(0.8) sepia(0.3) hue-rotate(240deg) saturate(1.5);
  transition: filter 0.3s ease, transform 0.3s ease;
}

.cp-game-link:hover .cp-platform-icon {
  filter: brightness(1) saturate(0) invert(1);
  transform: scale(1.1); /* Subtle scale effect on hover */
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .cp-contact-title {
    font-size: 3rem;
  }
  
  .cp-contact-form-wrapper,
  .cp-contact-image-container {
    width: 100%;
    padding-right: 0;
    margin-bottom: 2rem;
  }
  
  .cp-contact-info {
    flex-direction: column;
    align-items: center;
    margin-top: 3rem;
    padding: 1.5rem;
    width: 100%;
    clear: both;
    display: flex !important;
    visibility: visible !important;
    opacity: 1 !important;
  }
  
  .cp-contact-info-item {
    width: 100%;
    max-width: 100%;
    margin: 1rem 0;
    padding: 1.5rem;
    display: block !important;
    visibility: visible !important;
    opacity: 1 !important;
  }
  
  .cp-game-links {
    display: flex !important;
    visibility: visible !important;
    opacity: 1 !important;
  }
  
  .cp-game-link {
    display: flex !important;
    visibility: visible !important;
    opacity: 1 !important;
  }
  
  .cp-button-container {
    flex-direction: column; /* Stack vertically on mobile */
    align-items: center; /* Center align on mobile */
  }
  
  .cp-to-label {
    margin-bottom: 1rem; /* Increase spacing between logo and button */
    margin-left: 0; /* Remove left margin */
    justify-content: center; /* Center on mobile */
  }
  
  .cp-button-logo {
    width: 45px; /* Even larger on mobile for better visibility */
    height: 45px;
  }
  
  .cp-send-message-btn {
    width: 100%; /* Full width */
    max-width: 300px; /* Maximum width */
  }
  
  .cp-game-link {
    padding: 1rem 1.5rem; /* Slightly smaller padding on mobile */
    width: 100%; /* Full width */
    max-width: 300px; /* Maximum width */
    margin: 0 auto; /* Center the button */
  }
}

@media (min-width: 769px) and (max-width: 1024px) {
  .cp-send-message-btn {
    margin-right: 0; /* Remove right margin on tablet */
  }
  
  .cp-game-link {
    min-width: 180px; /* Slightly smaller minimum width on tablet */
  }
}

@media (max-width: 480px) {
  .cp-contact-title {
    font-size: 2.5rem;
  }
}

/* Footer styles for Contact Page */
.cp-site-footer {
  width: 100%;
  padding: 2rem 0;
  background-color: rgba(26, 20, 51, 0.8);
  margin-top: 4rem;
  border-top: 1px solid rgba(138, 43, 226, 0.3);
}

.cp-footer-content {
  max-width: 1200px;
  margin: 0 auto;
  text-align: center;
}

.cp-copyright {
  color: #e0e0ff;
  font-size: 0.9rem;
  font-family: 'Lato', sans-serif;
}