@import url('https://fonts.googleapis.com/css2?family=Cinzel:wght@700&family=Poppins:wght@400;700&display=swap');

html, body {
  margin: 0;
  padding: 0;
  height: 100%;
  min-height: 100vh;
  overflow-x: hidden;
  box-sizing: border-box;
  scroll-padding-top: 60px; /* NAVBAR STYLES */
  scroll-behavior: smooth; /* enable smooth scrolling */
}

body {
  padding-top: 60px; /* offset for fixed navbar */
  overflow-x: hidden;
}

/* Fix unwanted space below footer */
.site-footer {
  width: 100vw;
  margin-left: calc(-50vw + 50%);
  position: relative;
  left: 0;
  right: 0;
  bottom: 0;
  background: #2c1342;
  color: #fff;
}

html, body, #root, .App {
  height: 100%;
  min-height: 100vh;
}

body, .App {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  font-family: 'Poppins', serif;
  background: #ede6f6;
  width: 100%;
  overflow-x: hidden; /* Prevent horizontal scrolling */
  box-sizing: border-box;
  margin-bottom: 0 !important;
  padding-bottom: 0 !important;
  position: relative;
}

.fantasy-bg {
  background: #ede6f6;
  min-height: 100vh;
  width: 100vw;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  position: relative;
  left: 0;
  right: 0;
  box-sizing: border-box;
}

/* Navbar styles */
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: rgba(39, 29, 151, 0.75); /* transparent purple */
  z-index: 1000;
  box-shadow: 0 2px 16px 0 rgba(40,0,60,0.12);
  backdrop-filter: blur(6px);
  padding: 0;
  display: flex;
  align-items: center;
  height: 60px;
}

.navbar-logo-link {
  display: flex;
  align-items: center;
  text-decoration: none;
  margin-left: 32px;
}

.navbar-logo {
  font-family: 'NightForest', 'Segoe UI', Arial, sans-serif;
  font-size: 1.5rem;
  color: #fff;
  font-weight: bold;
  letter-spacing: 2px;
  user-select: none;
  cursor: pointer;
  text-shadow: 0 2px 8px #4a2a6d;
  white-space: nowrap;
  margin-left: 15px;
}

.navbar-cat-logo {
  height: 40px;
  width: auto;
  filter: brightness(0) invert(1); /* Makes the SVG white */
  transition: transform 0.3s ease;
  margin-left: 0;
}

.navbar-logo-link:hover .navbar-cat-logo {
  transform: scale(1.1);
}

.nav-links {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin: 0 32px 0 auto;
  padding: 0;
  list-style: none;
  height: 60px;
  flex: 1;
  gap: 32px; /* Add space between nav links */
}

.nav-links li {
  color: #fff;
  font-weight: 700;
  font-family: 'Poppins', sans-serif;
  cursor: pointer;
  font-size: 1.08rem;
  letter-spacing: 0.03em;
  transition: color 0.3s ease;
}

.nav-links li a,
.nav-links li .nav-button {
  color: #fff;
  text-decoration: none;
  transition: color 0.3s ease;
  font-weight: 700;
  font-family: 'Poppins', sans-serif;
  font-size: 1.08rem;
  letter-spacing: 0.03em;
}

.nav-button {
  background: none;
  border: none;
  padding: 0;
  cursor: pointer;
  color: #fff;
  font-weight: 700;
  font-family: 'FlameAndAsh', sans-serif;
  font-size: 1.08rem;
  letter-spacing: 0.03em;
}

.nav-links li:hover,
.nav-links li a:hover,
.nav-links li .nav-button:hover {
  color: #f87c7c; /* Pinkish/orange color for hover state */
}

.hero-section {
  text-align: center;
  margin-top: 0;
  padding: 0 2vw 2vw 2vw;
  padding-top: calc(100vh + 50px); /* Push content below the excavation area */
  display: grid;
  grid-template-columns: 1fr minmax(auto, 800px) 1fr; /* 3-column grid with fixed center column */
  min-height: 100vh;
  position: relative;
  overflow: hidden;
}
.hero-content {
  grid-column: 2; /* Center column */
}
.hero-title {
  font-family: 'Cinzel', serif;
  color: #353c7a;
  font-size: 3.5vw; /* Increased font size */
  font-weight: 700;
  letter-spacing: 0.03em;
  margin-bottom: 0.5vw;
  line-height: 1.2;
}
.hero-desc {
  font-family: 'Poppins', sans-serif;
  color: #2d2e4a;
  font-size: 1.3vw; /* Increased font size */
  margin-bottom: 1vw;
}
.hero-desc-small {
  font-family: 'Poppins', sans-serif;
  color: #2d2e4a;
  font-size: 1.1vw; /* Increased font size */
  margin-bottom: 2vw;
}
.about-section {
  position: relative;
  z-index: 10;
}
/* Mist effect will be layered under content */
.mist-canvas {
  position: fixed;
  top: 0; left: 0; width: 100vw; height: 100vh;
  pointer-events: none;
  z-index: 0;
  opacity: 0.55;
}
