/* HomePage.css */
@import url('./common.css');

/* Custom text selection color */
::selection {
  background-color: #b092d2;
  color: white;
}

::-moz-selection {
  background-color: #b092d2;
  color: white;
}

.about-container {
  width: 100%;
  padding: 0rem 0;
  background-color: #2e269a; /* Changed to rgb(57, 111, 246) with slight transparency */
  color: white;
  display: flex;
  justify-content: center;
  box-sizing: border-box;
  position: relative;
  overflow: hidden; /* Ensure the mist effects stay within the container */
}

/* Subtle mist effects for About Fablesmiths section */
.about-container::before, .about-container::after {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background-color: rgba(248, 217, 175, 0.05);
  opacity: 0.5;
  z-index: 0;
  animation: mist-movement-about 30s linear infinite;
  pointer-events: none;
  border-radius: 40%;
  will-change: transform;
}

.about-container::after {
  background-color: rgba(67, 4, 255, 0.02);
  animation: mist-movement-about-reverse 40s linear infinite;
  border-radius: 45%;
}

@keyframes mist-movement-about {
  0% {
    transform: rotate(0deg) scale(1);
  }
  100% {
    transform: rotate(360deg) scale(1.05);
  }
}

@keyframes mist-movement-about-reverse {
  0% {
    transform: rotate(0deg) scale(1.05);
  }
  100% {
    transform: rotate(-360deg) scale(1);
  }
}

.about-content {
  width: 100%;
  max-width: 100%;
  padding: 0;
  text-align: center;
}

.about-heading {
  font-size: 3.8rem;
  margin: 80px 0;
  padding-top: 2rem;
  padding-bottom: 0.5px;
  font-family: 'NightForest', 'ChalkaholicDemo', serif;
  font-weight: normal;
  letter-spacing: 0.03em;
  line-height: 1.3;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.4);
  color: #e3abff; /* Updated color */
  position: relative;
  display: inline-block;
}

/* Fancy decorative underline effect */
.about-heading::after {
  content: '';
  position: absolute;
  width: 200%;
  height: 35px;
  bottom: -45px; /* Further increased gap between header and underline */
  left: -50%;
  background-color: transparent;
  background-image: var(--underline-svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  opacity: 0.9; /* More saturated */
  filter: invert(75%) sepia(60%) saturate(2000%) hue-rotate(270deg) brightness(100%) contrast(110%);
}

.about-text {
  font-size: 1.2rem;
  line-height: 1.6;
  margin-top: 2.5rem;
  margin-bottom: 2rem;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  font-family: 'Lato', sans-serif;
  padding-bottom: 2rem;
}

.game-demo-placeholder {
  margin-top: 3rem;
  width: 100%;
  height: 300px;
  background-color: rgba(255, 255, 255, 0.1);
  border: 2px dashed rgba(255, 255, 255, 0.3);
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-family: 'CuteNCuddly','Cinzel', serif;
  font-size: 1.2rem;
}

.game-section {
  margin-top: 1.5rem;
  width: 100%;
  position: relative;
  overflow: hidden;
  border-radius: 8px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3);
}

.game-section.full-width {
  width: 100vw;
  margin-left: calc(-50vw + 50%);
  border-radius: 0;
}

.game-title {
  color: white;
  font-family: 'NightForest', 'SteakHouse', 'BrightGesture', serif;
  font-weight: normal;
  letter-spacing: 0.03em;
  line-height: 1.3;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.4);
  font-size: 2.8rem;
  margin-bottom: 1.5rem;
  text-align: center;
  position: relative;
  display: inline-block;
}

.game-title::after {
  content: '';
  position: absolute;
  width: 60%;
  height: 2px;
  bottom: -8px;
  left: 20%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.8), transparent);
}

.sneak-peek-heading {
  color: white;
  font-family: 'NightForest', 'ChalkaholicDemo', 'SteakHouse', 'BrightGesture', serif;
  font-weight: normal;
  letter-spacing: 0.03em;
  line-height: 1.3;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.4);
  font-size: 1.8rem;
  margin: 2rem 0 1.5rem;
  text-align: center;
  position: relative;
  display: inline-block;
}

.sneak-peek-heading::after {
  content: '';
  position: absolute;
  width: 60%;
  height: 2px;
  bottom: -8px;
  left: 20%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.8), transparent);
}

/* Game Teaser - Center below About section */
.game-teaser {
  width: 100vw;
  margin-left: calc(-50vw + 50%);
  background: linear-gradient(135deg, rgb(54, 50, 187), rgba(67, 58, 173, 0.9));
  border-top: 3px solid #6c5ffd;
  border-bottom: 3px solid #6c5ffd;
  box-shadow: 0 0 30px rgba(108, 95, 253, 0.6), 0 0 15px rgba(0, 0, 0, 0.5);
  padding: 40px 0;
  margin-top: 30px;
  z-index: 100;
  font-family: 'NightForest', 'ChalkaholicDemo', 'SteakHouse', 'BrightGesture', serif;
  font-weight: normal;
  letter-spacing: 0.03em;
  line-height: 1.3;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.4);
  position: relative;
  overflow: hidden;
  display: flex;
  justify-content: center;
}

/* Animated background */
.game-teaser::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(circle at 20% 30%, rgba(108, 95, 253, 0.15) 0%, transparent 50%),
    radial-gradient(circle at 80% 70%, rgba(213, 120, 255, 0.1) 0%, transparent 40%);
  z-index: -1;
  animation: background-shift 15s ease-in-out infinite alternate;
  will-change: background-position;
}

/* Animated mist layers */
.game-teaser::after {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background-color: rgba(108, 95, 253, 0.1);
  opacity: 0.6;
  z-index: -1;
  animation: mist-movement 30s linear infinite;
  will-change: transform;
  pointer-events: none;
}

/* Additional mist layer for more depth */
.game-teaser .teaser-content::before {
  content: '';
  position: absolute;
  top: -100%;
  left: -100%;
  width: 300%;
  height: 300%;
  background-color: rgba(213, 120, 255, 0.05);
  opacity: 0.4;
  z-index: -1;
  animation: mist-movement-reverse 20s linear infinite;
  will-change: transform;
  pointer-events: none;
}

@keyframes mist-movement {
  0% {
    transform: rotate(0deg) scale(1);
  }
  100% {
    transform: rotate(360deg) scale(1.1);
  }
}

@keyframes mist-movement-reverse {
  0% {
    transform: rotate(0deg) scale(1.1);
  }
  100% {
    transform: rotate(-360deg) scale(1);
  }
}

@keyframes background-shift {
  0% {
    background-position: 0% 0%;
  }
  50% {
    background-position: 100% 20%;
  }
  100% {
    background-position: 0% 40%;
  }
}

.teaser-content {
  max-width: 900px;
  width: 100%;
  padding: 0 20px;
  text-align: center;
  position: relative;
  z-index: 2;
}

.teaser-content h3 {
  color: #e3abff; /* Updated color */
  font-size: 3.8rem;
  margin: 15px 0;
  margin-bottom: 3rem;
  font-weight: normal;
  letter-spacing: 1px;
  position: relative;
  display: inline-block;
  font-family: 'NightForest', 'ChalkaholicDemo', 'SteakHouse', 'BrightGesture', serif;
  font-weight: normal;
  letter-spacing: 0.03em;
  line-height: 1.3;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.4);
}

/* Fancy decorative underline effect for teaser heading */
.teaser-content h3::after {
  content: '';
  position: absolute;
  width: 200%;
  height: 35px;
  bottom: -40px; /* Increased gap between header and underline */
  left: -50%;
  background-color: transparent;
  background-image: var(--underline-svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  opacity: 0.9; /* More saturated */
  filter: invert(75%) sepia(60%) saturate(2000%) hue-rotate(270deg) brightness(100%) contrast(110%);
}

.teaser-content p {
  color: white;
  font-size: 1.4rem;
  font-weight: bold;
  line-height: 1.6;
  margin-bottom: 1.5rem;
  font-family: 'Lato', sans-serif;
}

.rune-instructions {
  font-style: italic;
  color: #ffe674;
  font-size: 1.1rem;
  margin-bottom: 2rem;
}

.teaser-content p strong {
  font-size: 2.8rem;
  font-family: 'NightForest', 'SteakHouse','BrightGesture', 'Cinzel', serif;
  font-weight: normal;
  letter-spacing: 0.02em;
  line-height: 1.3;
  color: #d578ff; /*#f1975b; - orange original color*/
  /* text-shadow removed for readability */
}

.teaser-buttons {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-bottom: 10px;
}

.teaser-button {
  display: inline-block;
  padding: 25px 25px;
  margin: 0 10px;
  letter-spacing: 1px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  background-color: #6c5ffd;
  color: white;
  box-shadow: 0 4px 15px rgba(108, 95, 253, 0.4);
  font-family: 'OugkehRoundRegular', 'RhaegarwoodRegular','Lato', sans-serif;
  font-weight: thin;
  letter-spacing: 1.5px;
  margin-top: 1.5rem;
}

.teaser-button::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to right, transparent, rgba(255, 255, 255, 0.2), transparent);
  transform: translateX(-100%);
  transition: transform 0.6s ease;
}

.teaser-button:hover::after {
  transform: translateX(100%);
}

.yes-button {
  background-color: #6c5ffd; /* Consistent purple base color */
  color: white;
  box-shadow: 0 4px 15px rgba(108, 95, 253, 0.4);
  font-size: 1.8rem;
  padding: 25px 25px;
  animation: button-pulse 2s infinite alternate;
  font-family: 'OugkehRoundRegular','RhaegarwoodRegular', 'CuteNCuddly', cursive;
  font-weight: thin;
}

@keyframes button-pulse {
  0% {
    transform: translateY(0);
    box-shadow: 0 4px 15px rgba(108, 95, 253, 0.4);
  }
  100% {
    transform: translateY(-3px);
    box-shadow: 0 8px 25px rgba(108, 95, 253, 0.7);
  }
}

/* Button animations */
@keyframes nod-animation {
  0%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-3px);
  }
  80% {
    transform: translateY(2px);
  }
}

.yes-button:hover {
  background-color: #f8ee95; /*#f1975b; /* Salmon/orange color on hover */
  color: #3c3cab;
  box-shadow: 0 6px 20px rgba(241, 213, 91, 0.6);
  animation: nod-animation 0.8s ease-in-out infinite;
  will-change: transform;
}

.no-button {
  background: rgba(26, 28, 70, 0.8);
  color: #d8d8ff;
  border: 2px solid #6c5ffd;
  box-shadow: 0 4px 10px rgba(108, 95, 253, 0.2);
  font-size: 1.8rem;
  padding: 15px 25px;
  transition: all 0.3s ease;
}

.no-button:hover {
  background: rgba(26, 28, 70, 0.9);
  color: #d578ff; /*#f8ee95;*/ /*#f1975b;*/
  box-shadow: 0 4px 15px rgba(108, 95, 253, 0.3);
  animation: shake-animation 1.5s cubic-bezier(0.36, 0.07, 0.19, 0.97) infinite;
  will-change: transform;
}

@keyframes shake-animation {
  0% {
    transform: translateX(0) rotate(0deg);
  }
  15% {
    transform: translateX(-3px) rotate(-0.5deg);
  }
  30% {
    transform: translateX(2px) rotate(0.5deg);
  }
  45% {
    transform: translateX(-2px) rotate(-0.3deg);
  }
  60% {
    transform: translateX(1px) rotate(0.3deg);
  }
  75% {
    transform: translateX(-1px) rotate(-0.2deg);
  }
  100% {
    transform: translateX(0) rotate(0deg);
  }
}

/* Add responsive styling */
@media (max-width: 768px) {
  .about-text {
    font-size: 1.1rem;
  }
  
  .game-demo-placeholder {
    height: 200px;
  }
  
  .teaser-content h3 {
    font-size: 1.8rem;
  }
  
  .teaser-content p {
    font-size: 1.1rem;
  }
  
  .yes-button, .no-button {
    font-size: 1rem;
    padding: 12px 20px;
  }
}

