@media (max-width: 768px) {
  .team-member {
    flex: 0 0 calc(50% - 2rem);
  }
  
  .team-title {
    font-size: 3.8rem;
  }
}

@media (max-width: 480px) {
  .team-member {
    flex: 0 0 100%;
  }
  
  .member-avatar {
    width: 120px;
    height: 120px;
  }
}

/* Contact Section */
.contact-section {
  width: 100vw;
  margin-left: calc(-50vw + 50%);
  padding: 4rem 0; /* Increased vertical padding */
  padding-top: 10px; /* Extra padding to account for navbar */
  margin-top: -60px; /* Offset for the anchor positioning */
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  /*background-color: #5835e3;*/ /* Light purple background */
  display: flex;
  justify-content: center;
  box-sizing: border-box;
  position: relative;
  overflow: hidden;
}

.contact-content {
  width: 90%;
  max-width: 1200px;
  padding: 2rem 4rem; /* Increased horizontal padding */
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  position: relative;
  z-index: 1;
}

.contact-form-image-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%; /* Using full width */
  margin: 0.2rem auto; /* Further reduced margin to bring elements closer */
  gap: 1rem; /* Add gap to control spacing between form and image */
}

.contact-form-container {
  flex: 0 0 50%;
  padding: 0;
  width: 100%;
  max-width: 650px;
  margin: 0 auto;
}

.contact-title {
  font-family: 'NightForest', 'BrightGesture', 'Cinzel', serif;
  font-weight: normal;
  letter-spacing: 0.03em;
  padding-top: 2rem;
  line-height: 1.3;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.4);
  font-size: 3.8rem;
  margin: 30px 0;
  margin-top: 4rem;
  margin-bottom: 1rem;
  color: #e3abff; /* Updated color */
  position: relative;
  display: inline-block;
}

.contact-subtitle {
  font-size: 1.5rem;
  text-align: center;
  font-weight: bold;
  width: 90%;
  max-width: 650px;
  margin: 0 auto;
  margin-bottom: 0.5rem;
}

/* Fancy decorative underline effect for contact title */
.contact-title::after {
  content: '';
  position: absolute;
  width: 200%;
  height: 35px;
  bottom: -35px;
  left: -50%;
  background-color: transparent;
  background-image: var(--underline-svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  opacity: 0.9; /* More saturated */
  filter: invert(75%) sepia(60%) saturate(2000%) hue-rotate(270deg) brightness(100%) contrast(110%);
}

.contact-form {
  margin-top: 1px;
  padding: 1.5rem 2rem;
  background-color: rgba(72, 45, 140, 0.4);
  border-radius: 8px;
  width: 100%;
  max-width: 650px;
}

.form-group {
  margin-bottom: 1.5rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  font-size: 1rem;
  color: white;
  font-weight: bold;
  text-align: left;
  padding-left: 0.5rem;
}

.form-group input,
.form-group textarea {
  width: 100%;
  padding: 1rem;
  color:#e5d2ff;
  border: 2px solid #7038ff;
  border-radius: 6px;
  background-color: rgba(255, 255, 255, 0.1);
  font-family: inherit;
  font-size: 1.1rem;
  box-sizing: border-box;
}

.form-group textarea {
  resize: vertical;
}

.send-message-btn {
  width: 100%;
  padding: 0.8rem 2rem;
  font-family: 'OugkehRoundRegular','RhaegarwoodRegular', 'NightForest', 'Cinzel', serif;
  font-weight: normal;
  letter-spacing: 0.04em;
  line-height: 1.3;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
  font-size: 1.4rem;
  cursor: pointer;
  transition: all 0.3s ease;
  box-sizing: border-box;
  border-radius: 8px;
  background-color: #6c5ffd;
  color: rgba(255, 255, 255);
  box-shadow: 0 4px 8px rgba(108, 95, 253, 0.4);
}

.send-message-btn:hover {
  background-color: #e6dc4b; /* Yellow on hover */
  color: #2c2ca0;
  transform: translateY(-2px);
  box-shadow: 0 6px 12px rgba(230, 220, 75, 0.5);
}

/* Form success/error messages */
.form-message {
  margin-top: 1.5rem;
  padding: 1rem;
  border-radius: 8px;
  font-weight: 500;
  text-align: center;
  animation: fadeIn 0.5s ease-in-out;
  clear: both;
  width: 80%;
  max-width: 500px;
  margin-left: auto;
  margin-right: auto;
}

.success-message {
  background-color: rgba(76, 175, 80, 0.3);
  color: #1b9e25;
  border: 1px solid rgba(76, 175, 80, 0.5);
  box-shadow: 0 0 15px rgba(76, 175, 80, 0.3);
  font-weight: 600;
}

.error-message {
  background-color: rgba(244, 67, 54, 0.3);
  color: #ff5252;
  border: 1px solid rgba(244, 67, 54, 0.5);
  box-shadow: 0 0 15px rgba(244, 67, 54, 0.3);
  font-weight: 600;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(-10px); }
  to { opacity: 1; transform: translateY(0); }
}

.contact-image-container {
  flex: 0 0 30%; /* Increased from 25% to 30% to make image container bigger */
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 0; /* Removed margin to bring closer to form */
}

.contact-image {
  max-width: 120%; /* Increased from 40% to 75% to make Urska image bigger */
  height: auto;
  border: none;
  box-shadow: none;
  transition: max-width 0.3s ease; /* Smooth transition when scaling */
}

/* Responsive adjustments for contact section */
@media (max-width: 992px) {
  .contact-form-image-container {
    width: 95%;
    flex-direction: column;
    margin: 0.5rem auto;
  }
  
  .contact-form-container {
    flex: 0 0 100%;
    padding: 0;
    margin-bottom: 1rem;
    max-width: 90%;
    width: 90%;
  }
  
  .contact-form {
    padding: 1.2rem;
  }
  
  .contact-image-container {
    display: none; /* Hide Urska image on mobile */
  }
  
  .contact-title {
    font-size: 2.5rem;
    margin-top: 4rem;
    margin-bottom: 1.5rem;
  }
  
  .contact-content {
    padding: 1rem 2rem;
  }
  
  .form-group {
    margin-bottom: 1rem; /* Reduce spacing between form fields */
  }
}

@media (max-width: 768px) {
  .contact-title {
    font-size: 2rem;
    margin-top: 3rem;
    margin-bottom: 1rem;
  }
  
  .form-group input,
  .form-group textarea {
    padding: 0.6rem;
  }
  
  .contact-content {
    padding: 1rem;
  }
  
  .contact-subtitle {
    margin-bottom: 0 !important;
  }
}

@media (max-width: 480px) {
  .contact-title {
    font-size: 1.8rem;
    margin-top: 2rem;
  }
  
  .form-group {
    margin-bottom: 0.8rem;
  }
}
