/* Minimal reset for fantasy landing page */
html, body, #root {
  height: 100%;
  margin: 0;
  padding: 0;
  min-height: 100vh;
  background: #ede6f6;
}

/* Custom scrollbar styling */
::-webkit-scrollbar {
  width: 12px; /* Width of the scrollbar */
}

::-webkit-scrollbar-track {
  background: #3d3295; /* Deep purple background */
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background: #b585e4; /* Light purple scrollbar thumb */
  border-radius: 10px;
  border: 2px solid rgba(255, 255, 255, 0.5); /* Light border for better visibility */
}

::-webkit-scrollbar-thumb:hover {
  background: #9e6ad0; /* Slightly darker purple on hover */
}

/* Custom cursor styling handled by CustomCursor component */

/* For Firefox */
* {
  scrollbar-width: thin;
  scrollbar-color: #b585e4 #3d3295;
}
