.custom-cursor {
  position: fixed;
  width: 90px; /* Further increased size */
  height: 90px; /* Further increased size */
  pointer-events: none;
  z-index: 9999;
  will-change: transform;
  /* Using translate3d for hardware acceleration */
  transform: translate3d(0, 0, 0);
  /* Remove transition for smoother movement - now handled by requestAnimationFrame */
  top: 0;
  left: 0;
}

.cursor-outline {
  width: 100%;
  height: 100%;
  border: none; /* Removed border completely */
  padding: 0;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
}

.custom-cursor img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  /* Ensure the pen tip aligns with the actual cursor */
  transform: rotate(0deg);
}

.custom-cursor.clicked img {
  animation: pen-press 0.2s cubic-bezier(0.215, 0.610, 0.355, 1.000);
}

@keyframes pen-press {
  0% { transform: rotate(0deg); }
  50% { transform: rotate(-3deg) scale(0.98); }
  100% { transform: rotate(0deg); }
}

.custom-cursor.hovering img {
  animation: pen-wiggle 0.6s cubic-bezier(0.445, 0.05, 0.55, 0.95) infinite alternate;
}

@keyframes pen-wiggle {
  0% {
    transform: rotate(-5deg);
  }
  25% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(5deg);
  }
  75% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(-5deg);
  }
}

/* Hide default cursor when custom cursor is active */
body.custom-cursor-active,
body.custom-cursor-active *,
body.custom-cursor-active a,
body.custom-cursor-active button,
body.custom-cursor-active input,
body.custom-cursor-active .game-canvas {
  cursor: none !important;
}

/* Hide custom cursor on touch devices */
@media (hover: none) {
  .custom-cursor {
    display: none !important;
  }
}
