.image-loading {
  position: relative;
  overflow: hidden;
}

.image-placeholder-pulse {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(90deg, rgba(46, 38, 154, 0.8), rgba(138, 43, 226, 0.4), rgba(46, 38, 154, 0.8));
  background-size: 200% 100%;
  animation: pulse-animation 1.5s infinite;
}

@keyframes pulse-animation {
  0% {
    background-position: 100% 0%;
  }
  100% {
    background-position: 0% 0%;
  }
}
